/* eslint-disable react/prop-types */

import React from 'react';
import Button from '../Button';

import style from './order.module.scss';

function Order({ order, buttonName, onClickHandler }) {
  const { point, model } = order;
  return (
    <div className={style.content}>
      <div className={style.yourOrder}> :</div>
      <div className={style.params}>
        <span className={style.paramsName}> </span>
        <div className={style.empty}>{}</div>
        <div className={style.description}>
          <div className={style.city}>
            {point.city ? point.city : ' '}
          </div>
          <div className={style.street}>
            {point.street ? point.street : ' '}
          </div>
        </div>
      </div>

      {model && (
        <div className={style.params}>
          <span className={style.paramsName}></span>
          <div className={style.empty}>{}</div>
          <div className={style.description}>
            <div>{model}</div>
          </div>
        </div>
      )}

      <div className={style.params}>
        <span className={style.paramsName}></span>
        <div className={style.empty}>{}</div>
        <div className={style.description}>
          <div>{}</div>
        </div>
      </div>
      <div className={style.params}>
        <span className={style.paramsName}> </span>
        <div className={style.empty}>{}</div>
        <div className={style.description}>
          <div>1 2{}</div>
        </div>
      </div>
      <div className={style.params}>
        <span className={style.paramsName}></span>
        <div className={style.empty}>{}</div>
        <div className={style.description}>
          <div> {}</div>
        </div>
      </div>
      <div className={style.params}>
        <span className={style.paramsName}> </span>
        <div className={style.empty}>{}</div>
        <div className={style.description}>
          <div>{}</div>
        </div>
      </div>

      <div className={style.price}>
        <span>
          <b>:</b>
        </span>
        <span>  8000  12000 &#x20bd;</span>
      </div>
      <div className={style.button}>
        {buttonName
          .filter((item) => item.id === order.step)
          .map((item) => (
            <Button
              key={item.id}
              name={item.name}
              disabled={order.lastStepValidate < item.id}
              onClickHandler={() => onClickHandler && onClickHandler(item.id)}
            />
          ))}
      </div>
    </div>
  );
}

export default Order;
